import { LoaderFunction, useLoaderData } from "remix";
import { db } from "~/utils/db.server";

type LoaderData = {
  joke: { id: string; name: string; completed: boolean; createdAt: any };
};
export const loader: LoaderFunction = async ({ params }) => {
  const joke = await db.todo.findUnique({ where: { id: params.todoId } });
  if (!joke) throw new Error("Todo item not found");
  const data: LoaderData = { joke };
  return data;
};

export default function TodoItem() {
  const data = useLoaderData<LoaderData>();
  return (
    <div>
      <h3>{data.joke.name}</h3>
      {data.joke.completed ? (
        <p>Status : Not Completed!</p>
      ) : (
        <p>Status : Completed!</p>
      )}
      <p>Created At: {data.joke.createdAt}</p>
    </div>
  );
}
